import { Attrs } from "./Attrs";
import { makeReactive } from "./makeReactive";

export function createNodeComponent<T extends Attrs>(
  C: new (attrs: T) => Kurtka.Component,
  attrs: T
): Kurtka.NodeComponent {
  const component = new C(attrs);
  if (component.state) {
    makeReactive(component.state, component.update.bind(component));
  }

  component.node = component.render();
  return component.update.bind(component);
}
