import { Tags } from "./Tags";
import { Attrs } from "./Attrs";

type Childrens = (Kurtka.Node | (() => Kurtka.Node[]))[];

function renderElements(elements: Childrens): Node[] {
  const result: Node[] = [];
  for (let i = 0; i < elements.length; i++) {
    const node = elements[i];
    if (typeof node === "function") {
      const nodeFnResult = node();
      if (Array.isArray(nodeFnResult)) {
        result.push(...renderElements(nodeFnResult));
      } else {
        result.push(nodeFnResult);
      }
    } else {
      result.push(document.createTextNode(String(node)));
    }
  }
  return result;
}

export function createNodeHtml(
  tag: Tags,
  attrs: Attrs,
  childrens: Childrens
): Kurtka.Node {
  const rootEl = document.createElement(tag);
  if (attrs) {
    const { events, style, ...attributes } = attrs;
    Object.assign(rootEl, attributes);
    Object.assign((rootEl as HTMLElement).style, style);
    Object.entries(events || {}).forEach(([event, handler]) => {
      rootEl.addEventListener(event, (...args) => handler(...args));
    });
  }

  return function renderHtml(): HTMLElement {
    const elements = renderElements(childrens);
    rootEl.append(...elements);
    const left = rootEl.childNodes.length - elements.length;
    for (let i = 0; i < left; i++) {
      rootEl.childNodes[0].remove();
    }
    return rootEl;
  };
}
